#app {
  height: 100%;
  overflow: hidden;
}
/*插件 简单reset*/
* {
  margin: 0;
  padding: 0;
}
/*必须，关系到单个page能否全屏显示*/
html,
body {
  height: 100%;
  overflow: hidden;
}
#container,
.sections,
.section {
  /*必须，兼容，在浏览器不支持transform属性时，通变top/left完成滑动动画*/
  position: relative;
  /*必须，关系到单个page能否全屏显示*/
  height: 100%;
}
.section {
  /*有背景图时必须，关系到背景图能够全屏显示*/
  background-color: #fff;
  background-size: cover;
  background-position: 50% 50%;
}
/*以下样式用来设置slider样式，可自行修改*/
.pages {
  position: fixed;
  right: 20px;
  top: 50%;
  list-style: none;
}
.pages li {
  width: 6px;
  height: 8px;
  background-color: #ffc400;
  border-radius: 50% 10%;
  margin: 15px 0 0 7px;
}
.pages li.active {
  margin-left: 4px;
  background-color: #fff;
  border: 4px solid #ffc400;
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
